.text-animate {
    display: inline-block;
    opacity: 0;
    animation: bounceIn;
    animation-duration: 1s;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    min-width: 10px;
  
    .text-animate-hover {
      min-width: 10px;
      display: inline-block;
      animation-fill-mode: both;
    
    
    }
  
    &:hover {
      animation: bounceOut 1s;
      color: #ffd700;
    }
    
  }
  
  
  @for $i from 1 through 52 {
    .text-animate._#{$i} {
      animation-delay: #{$i / 10}s;
    }
  }
  
  
  
  @for $i from 1 through 52 {
    .text-animate-hover._#{$i} {
      animation-delay: #{$i / 10}s;
    }
  }
  