// @import 'responsive';
@import 'colors';
.nav-bar {
  
  //temporary top padding
  // padding-top: 32px;
  background:$main-color;
  width: 80px;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  min-height: 500px;

  .logo {
    display: block;
    padding: 8px 0;
    font-size: 2px;

    img {
      display: block;
      margin: 8px auto;
      width: 24px;
      height: auto;

      &.sub-logo {
        width: 50px;
      }
    }
  }

  nav {
    display: block;
    text-align: center;
    position: absolute;
    height: 210px;
    top: 50%;
    margin-top: -180px;
    width: 100%;
 
   

    a {
      font-size: 22px;
      color: #4d4d4e;
      display: block;
      line-height: 51px;
      height: 51px;
      position: relative;
      text-decoration: none;

      i {
        transition: all 0.3s ease-out;
      }

      &:hover {
        color: #ffd700;

        svg {
          opacity: 0;
        }

        &:after {
          opacity: 1;
        }
      }

      &:after {
        content: '';
        font-size: 9px;
        letter-spacing: 2px;
        position: absolute;
        bottom: 0;
        display: block;
        width: 100%;
        text-align: center;
        opacity: 0;
        -webkit-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;
      }

      &:first-child {
        &:after {
          content: 'HOME';
        }
      }
    }

    a.about-link {
      &:after {
        content: 'ABOUT';
      }
    }

    a.contact-link {
      &:after {
        content: 'CONTACT';
      }
    }

    a.portfolio-link {
      &:after {
        content: 'PORTFOLIO';
      }
      
    }
    a.education-link {
      &:after {
        content: 'EDUCATION';
      }
      
    }
 
    a.coding-link {
      &:after {
        content: 'CODING';
      }
      
    }


    a.active {
      svg {
        color: $primary-color;
      }
    }
  }

  ul {
    position: absolute;
    bottom: 20px;
    width: 100%;
    display: block;
    padding: 0;
    list-style: none;
    text-align: center;
    margin: 0;

    li {
      a {
        padding: 7px 0;
        display: block;
        font-size: 15px;
        line-height: 16px;
        color: #4d4d4e;

        .anchor-icon {
          font-size: 2rem;
      }
      }

      &:hover {
        .anchor-icon {
          color: $primary-color;
        }
      }
    }
  }
}


.hamburger-icon, .close-icon {
  display: none;
}


@media screen and (max-width: 1200px) {
  .nav-bar { 
    background: rgba(24, 23, 23, 0.5); // 0.5 is opacity
    position: initial;
    height: auto;
    min-height: auto;
   


     ul, nav {
      display: none;
     }

     nav {
      width: 100%;
      top: 0;
      left: 0;
      background:$main-color;
      height: 100%;
      margin: 0;
      z-index: 3;
      position: fixed;

      &.mobile-show {
        display: block;
        width: 40%;
        height: 96%;
        z-index: 3;
      }
      .nav-mob {
        margin-top: 5rem;
        margin-left: 15px;
        overflow-y: scroll;
      }

      a {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      a:after {
        opacity: 1;
        position: initial;
        width: auto;
        margin-left: 10PX;
      }

      a svg {
        opacity: 1 !important;
      }
     }
    
      
  }

     .hamburger-icon,
     .close-icon {
      position: fixed !important;
      top: 15px;
      right: 15px;
      display: block;
      font-size: 3rem;
      z-index: 2;
   }
 
     

     .copyright {
     display: none;
    }
  }
  
  .copyright {
    position: fixed;
    font-size: 1rem;
    bottom: 1rem;
    right: 1rem;
    text-align: left;
    color: $primary-color;
  }